* {
  margin: 0;
  padding: 0;
}

body {
  height: 100%;
  background-color: #e7dfdd;
  flex-flow: column;
  gap: 20px;
  padding: 20px;
  display: flex;
}

.is-hidden {
  display: none;
}

.breed-select {
  width: 500px;
}

.loading-cat {
  width: 300px;
  height: 300px;
  margin: 48px auto;
  position: relative;
}

.cat-body {
  width: 290px;
  height: 290px;
  background-color: #eba764;
  border: 5px solid #514e51;
  border-radius: 50%;
  position: absolute;
}

.cat-body:before {
  content: "";
  width: 188px;
  height: 188px;
  background-color: #e7dfdd;
  border: 5px solid #514e51;
  border-radius: 50%;
  position: absolute;
  top: calc(50% - 99px);
  left: calc(50% - 99px);
}

.cat-body:after {
  content: "";
  width: 222px;
  height: 222px;
  border: 22px solid #f1c28f;
  border-radius: 50%;
  position: absolute;
  top: calc(50% - 133px);
  left: calc(50% - 133px);
}

.cat-animation-mask {
  width: 50%;
  height: 50%;
  transform-origin: 100% 100%;
  background-color: #e7dfdd;
  position: absolute;
}

.cat-animation-mask:before {
  content: "";
  width: 100%;
  height: 100%;
  transform-origin: 0 100%;
  background-color: #e7dfdd;
  position: absolute;
  left: 100%;
}

.cat-animation-mask:after {
  content: "";
  width: 100%;
  height: 100%;
  transform-origin: 100% 0;
  background-color: #e7dfdd;
  position: absolute;
  top: 100%;
}

.cat-head {
  width: 50%;
  height: 50%;
  transform-origin: 0 100%;
  background-color: #e7dfdd;
  position: absolute;
  top: 0;
  right: 0;
}

.cat-head .cat-face {
  width: 50px;
  height: 40px;
  background-color: #eba764;
  border: 5px solid #514e51;
  border-bottom: 0;
  position: absolute;
  bottom: 0;
  right: 0;
}

.cat-head .cat-face:before {
  content: "";
  width: 22px;
  height: 22px;
  background-color: #f1c28f;
  border-radius: 50%;
  position: absolute;
  bottom: -15px;
  left: calc(50% - 10px);
}

.cat-head .cat-ear {
  width: 10px;
  height: 12px;
  background-color: #eba764;
  border: 5px solid #514e51;
  border-bottom: 0;
  border-radius: 20px 0 0;
  position: absolute;
  bottom: 39px;
  right: 0;
}

.cat-head .cat-ear:before {
  content: "";
  width: 10px;
  height: 12px;
  background-color: #eba764;
  border: 5px solid #514e51;
  border-bottom: 0;
  border-radius: 0 20px 0 0;
  position: absolute;
  top: -5px;
  left: -45px;
}

.cat-head .cat-hand {
  width: 10px;
  height: 30px;
  background-color: #eba764;
  border: 5px solid #514e51;
  border-top: 0;
  border-radius: 0 0 10px 10px;
  position: absolute;
  bottom: -32px;
  right: 0;
}

.cat-head .cat-hand:before {
  content: "";
  width: 10px;
  height: 30px;
  background-color: #eba764;
  border: 5px solid #514e51;
  border-top: 0;
  border-radius: 0 0 10px 10px;
  position: absolute;
  top: 0;
  left: -45px;
}

.cat-head .cat-eye, .cat-head .cat-eye-light {
  width: 6px;
  height: 6px;
  background-color: #514e51;
  border-radius: 3px;
  position: absolute;
  top: 116px;
  right: 12px;
}

.cat-head .cat-eye:before, .cat-head .cat-eye-light:before {
  content: "";
  width: 6px;
  height: 6px;
  background-color: #514e51;
  border-radius: 3px;
  position: absolute;
  top: 0;
  right: 30px;
}

.cat-head .cat-eye-light, .cat-head .cat-eye-light:before {
  opacity: 0;
  background-color: #fff;
  animation: eye-light-animation 2.5s infinite;
  box-shadow: 0 0 10px #fff;
}

.cat-head .cat-mouth {
  width: 2px;
  height: 0;
  background-color: #fd7667;
  border: 2px solid #514e51;
  border-top: 0;
  border-radius: 0 0 10px 10px;
  animation: mouth-animation 2.5s infinite;
  position: absolute;
  bottom: 16px;
  right: 27px;
}

.cat-head .cat-mouth:before {
  content: "";
  width: 2px;
  height: 2px;
  background-color: #eba764;
  border: 2px solid #514e51;
  border-top: 0;
  border-radius: 0 0 10px 10px;
  position: absolute;
  top: 0;
  right: -4px;
}

.cat-head .cat-mouth:after {
  content: "";
  width: 2px;
  height: 2px;
  background-color: #eba764;
  border: 2px solid #514e51;
  border-top: 0;
  border-radius: 0 0 10px 10px;
  position: absolute;
  top: 0;
  right: 0;
}

.cat-head .cat-beard {
  width: 12px;
  height: 3px;
  background-color: #514e51;
  position: absolute;
  bottom: 18px;
  right: -4px;
}

.cat-head .cat-beard:before {
  content: "";
  width: 12px;
  height: 3px;
  background-color: #514e51;
  position: absolute;
  top: -5px;
  right: 0;
  transform: rotate(-10deg);
}

.cat-head .cat-beard:after {
  content: "";
  width: 12px;
  height: 3px;
  background-color: #514e51;
  position: absolute;
  top: 5px;
  right: 0;
  transform: rotate(10deg);
}

.cat-head .cat-beard.right {
  right: 52px;
}

.cat-head .cat-beard.right:before {
  transform: rotate(10deg);
}

.cat-head .cat-beard.right:after {
  transform: rotate(-10deg);
}

.cat-foot {
  width: 50%;
  height: 50%;
  transform-origin: 100% 0;
  background-color: #e7dfdd;
  position: absolute;
  bottom: 0;
  left: 0;
}

.cat-foot .cat-belly {
  width: 14px;
  height: 46px;
  background-color: #eba764;
  border: 5px solid #514e51;
  border-left: 0;
  border-right: 0;
  position: absolute;
  bottom: 0;
  right: 0;
}

.cat-foot .cat-leg {
  width: 30px;
  height: 10px;
  background-color: #eba764;
  border: 5px solid #514e51;
  border-right: 0;
  border-radius: 10px 0 0 10px;
  position: absolute;
  bottom: 0;
  right: 14px;
}

.cat-foot .cat-leg:before {
  content: "";
  width: 30px;
  height: 10px;
  background-color: #eba764;
  border: 5px solid #514e51;
  border-right: 0;
  border-radius: 10px 0 0 10px;
  position: absolute;
  top: -41px;
  right: 0;
}

.cat-foot .cat-tail {
  width: 50px;
  height: 14px;
  background-color: #c48344;
  border: 5px solid #514e51;
  border-right: 0;
  border-radius: 14px 0 0 14px;
  position: absolute;
  bottom: 16px;
  right: 14px;
}

.cat-foot .cat-tail:after {
  content: "";
  width: 22px;
  height: 22px;
  background-color: #f1c28f;
  border-radius: 50%;
  position: absolute;
  bottom: -4px;
  right: -28px;
}

.cat-foot .cat-tail:before {
  content: "";
  width: 10px;
  height: 18px;
  background-color: #eba764;
  border: 5px solid #514e51;
  border-right: 0;
  border-radius: 12px 0 0 12px;
  position: absolute;
  bottom: -7px;
  right: 0;
}

.cat-animation-mask {
  animation: mask-animation 2.5s infinite;
  transform: rotate(45deg);
}

.cat-animation-mask:before {
  animation: mask-animation-sub-right 2.5s infinite;
}

.cat-animation-mask:after {
  animation: mask-animation-sub-left 2.5s infinite;
}

.cat-head {
  animation: head-animation 2.5s cubic-bezier(.2, 0, .09, 1) infinite;
  transform: rotate(70deg);
}

.cat-foot {
  animation: foot-animation 2.5s cubic-bezier(.2, 0, .45, 1) infinite;
  transform: rotate(25deg);
}

.cat-info {
  width: 100%;
  height: 80%;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
  display: flex;
}

.cat-info__img {
  width: 100%;
  height: calc(40% - 20px);
  border: 2px solid #000;
  border-radius: 5px;
  overflow: scroll;
}

.cat-info__img-size {
  width: 100%;
}

.cat-info__info {
  width: 100%;
  height: calc(20% - 20px);
  flex-flow: column;
  gap: 20px;
  display: flex;
}

@keyframes mask-animation {
  0% {
    transform: rotate(45deg);
  }

  100% {
    transform: rotate(-675deg);
  }
}

@keyframes mask-animation-sub-left {
  0% {
    transform: rotate(0deg);
  }

  50% {
    transform: rotate(90deg);
  }

  100% {
    transform: rotate(0deg);
  }
}

@keyframes mask-animation-sub-right {
  0% {
    transform: rotate(0deg);
  }

  50% {
    transform: rotate(-90deg);
  }

  100% {
    transform: rotate(0deg);
  }
}

@keyframes head-animation {
  0% {
    transform: rotate(70deg);
  }

  100% {
    transform: rotate(-650deg);
  }
}

@keyframes foot-animation {
  0% {
    transform: rotate(25deg);
  }

  100% {
    transform: rotate(-695deg);
  }
}

@keyframes eye-light-animation {
  0% {
    opacity: 0;
    height: 6px;
  }

  50% {
    opacity: .75;
    height: 50px;
  }

  80% {
    opacity: 1;
    height: 6px;
  }

  100% {
    opacity: 0;
  }
}

@keyframes mouth-animation {
  0% {
    height: 0;
  }

  50% {
    height: 10px;
  }

  100% {
    height: 0;
  }
}

:root {
  --ss-primary-color: #5897fb;
  --ss-bg-color: #fff;
  --ss-font-color: #4d4d4d;
  --ss-font-placeholder-color: #8d8d8d;
  --ss-disabled-color: #dcdee2;
  --ss-border-color: #dcdee2;
  --ss-highlight-color: #fffb8c;
  --ss-success-color: #00b755;
  --ss-error-color: #dc3545;
  --ss-main-height: 30px;
  --ss-content-height: 300px;
  --ss-spacing-l: 7px;
  --ss-spacing-m: 5px;
  --ss-spacing-s: 3px;
  --ss-animation-timing: .2s;
  --ss-border-radius: 4px;
}

@keyframes ss-valueIn {
  0% {
    opacity: 0;
    transform: scale(0);
  }

  100% {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes ss-valueOut {
  0% {
    opacity: 1;
    transform: scale(1);
  }

  100% {
    opacity: 0;
    transform: scale(0);
  }
}

.ss-hide {
  display: none !important;
}

.ss-main {
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  color: var(--ss-font-color);
  min-height: var(--ss-main-height);
  width: 100%;
  padding: var(--ss-spacing-s);
  cursor: pointer;
  border: 1px solid var(--ss-border-color);
  border-radius: var(--ss-border-radius);
  background-color: var(--ss-bg-color);
  box-sizing: border-box;
  transition: background-color var(--ss-animation-timing);
  outline: 0;
  flex-direction: row;
  display: flex;
  position: relative;
  overflow: hidden;
}

.ss-main:focus {
  box-shadow: 0 0 5px var(--ss-primary-color);
}

.ss-main.ss-disabled {
  background-color: var(--ss-disabled-color);
  cursor: not-allowed;
}

.ss-main.ss-disabled .ss-values .ss-disabled {
  color: var(--ss-font-color);
}

.ss-main.ss-disabled .ss-values .ss-value .ss-value-delete {
  cursor: not-allowed;
}

.ss-main.ss-open-above {
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}

.ss-main.ss-open-below {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}

.ss-main .ss-values {
  gap: var(--ss-spacing-m);
  flex-wrap: wrap;
  flex: 100%;
  display: inline-flex;
}

.ss-main .ss-values .ss-placeholder {
  padding: var(--ss-spacing-s) var(--ss-spacing-m) var(--ss-spacing-s) var(--ss-spacing-m);
  width: 100%;
  color: var(--ss-font-placeholder-color);
  text-overflow: ellipsis;
  white-space: nowrap;
  align-items: center;
  margin: auto 0;
  line-height: 1em;
  display: flex;
  overflow: hidden;
}

.ss-main .ss-values .ss-max {
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  width: fit-content;
  color: var(--ss-bg-color);
  padding: var(--ss-spacing-s) var(--ss-spacing-m);
  background-color: var(--ss-primary-color);
  border-radius: var(--ss-border-radius);
  align-items: center;
  font-size: 12px;
  line-height: 1;
  display: flex;
}

.ss-main .ss-values .ss-single {
  margin: auto 0px auto var(--ss-spacing-s);
  display: flex;
}

.ss-main .ss-values .ss-value {
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  width: fit-content;
  background-color: var(--ss-primary-color);
  border-radius: var(--ss-border-radius);
  animation-name: ss-valueIn;
  animation-duration: var(--ss-animation-timing);
  align-items: center;
  animation-timing-function: ease-out;
  animation-fill-mode: both;
  display: flex;
}

.ss-main .ss-values .ss-value.ss-value-out {
  animation-name: ss-valueOut;
  animation-duration: var(--ss-animation-timing);
  animation-timing-function: ease-out;
}

.ss-main .ss-values .ss-value .ss-value-text {
  color: var(--ss-bg-color);
  padding: var(--ss-spacing-s) var(--ss-spacing-m);
  font-size: 12px;
  line-height: 1;
}

.ss-main .ss-values .ss-value .ss-value-delete {
  height: var(--ss-spacing-l);
  width: var(--ss-spacing-l);
  padding: var(--ss-spacing-s) var(--ss-spacing-m);
  cursor: pointer;
  border-left: solid 1px var(--ss-bg-color);
  box-sizing: content-box;
  align-items: center;
  display: flex;
}

.ss-main .ss-values .ss-value .ss-value-delete svg {
  height: var(--ss-spacing-l);
  width: var(--ss-spacing-l);
}

.ss-main .ss-values .ss-value .ss-value-delete svg path {
  fill: none;
  stroke: var(--ss-bg-color);
  stroke-width: 18px;
  stroke-linecap: round;
  stroke-linejoin: round;
}

.ss-main .ss-deselect {
  width: 8px;
  height: 8px;
  margin: 0 var(--ss-spacing-m) 0 var(--ss-spacing-m);
  flex: 0 auto;
  justify-content: flex-end;
  align-self: center;
  display: flex;
}

.ss-main .ss-deselect svg {
  width: 8px;
  height: 8px;
}

.ss-main .ss-deselect svg path {
  fill: none;
  stroke: var(--ss-font-color);
  stroke-width: 20px;
  stroke-linecap: round;
  stroke-linejoin: round;
}

.ss-main .ss-arrow {
  width: 12px;
  height: 12px;
  margin: auto var(--ss-spacing-m) auto var(--ss-spacing-m);
  flex: 0 auto;
  justify-content: flex-end;
  align-items: center;
  display: flex;
}

.ss-main .ss-arrow path {
  fill: none;
  stroke: var(--ss-font-color);
  stroke-width: 18px;
  stroke-linecap: round;
  stroke-linejoin: round;
  transition-timing-function: ease-out;
  transition: var(--ss-animation-timing);
}

.ss-content {
  height: auto;
  width: auto;
  max-height: var(--ss-content-height);
  box-sizing: border-box;
  border: solid 1px var(--ss-border-color);
  background-color: var(--ss-bg-color);
  transition: transform var(--ss-animation-timing), opacity var(--ss-animation-timing);
  opacity: 0;
  transform-origin: top;
  z-index: 10000;
  flex-direction: column;
  display: flex;
  position: absolute;
  overflow: hidden;
  transform: scaleY(0);
}

.ss-content.ss-relative {
  height: 100%;
  position: relative;
}

.ss-content.ss-open-above {
  opacity: 1;
  transform-origin: bottom;
  border-top-left-radius: var(--ss-border-radius);
  border-top-right-radius: var(--ss-border-radius);
  flex-direction: column-reverse;
  transform: scaleY(1);
}

.ss-content.ss-open-below {
  opacity: 1;
  transform-origin: top;
  border-bottom-left-radius: var(--ss-border-radius);
  border-bottom-right-radius: var(--ss-border-radius);
  transform: scaleY(1);
}

.ss-content .ss-search {
  padding: var(--ss-spacing-l) var(--ss-spacing-l) var(--ss-spacing-m) var(--ss-spacing-l);
  flex-direction: row;
  flex: 0 auto;
  display: flex;
}

.ss-content .ss-search input {
  font-size: inherit;
  line-height: inherit;
  width: 100%;
  min-width: 0;
  padding: var(--ss-spacing-m) var(--ss-spacing-l);
  border: 1px solid var(--ss-border-color);
  border-radius: var(--ss-border-radius);
  background-color: var(--ss-bg-color);
  text-align: left;
  box-sizing: border-box;
  outline: 0;
  flex: auto;
  margin: 0;
  display: inline-flex;
}

.ss-content .ss-search input::-ms-input-placeholder {
  color: var(--ss-font-placeholder-color);
  vertical-align: middle;
}

.ss-content .ss-search input::placeholder {
  color: var(--ss-font-placeholder-color);
  vertical-align: middle;
}

.ss-content .ss-search input:focus {
  box-shadow: 0 0 5px var(--ss-primary-color);
}

.ss-content .ss-search .ss-addable {
  cursor: pointer;
  height: auto;
  margin: 0 0 0 var(--ss-spacing-m);
  border: 1px solid var(--ss-border-color);
  border-radius: var(--ss-border-radius);
  flex: none;
  justify-content: center;
  align-items: center;
  display: inline-flex;
}

.ss-content .ss-search .ss-addable svg {
  width: 12px;
  height: 12px;
  margin: auto var(--ss-spacing-m) auto var(--ss-spacing-m);
  flex: 0 auto;
  justify-content: flex-end;
  align-items: center;
  display: flex;
}

.ss-content .ss-search .ss-addable svg path {
  fill: none;
  stroke: var(--ss-font-color);
  stroke-width: 18px;
  stroke-linecap: round;
  stroke-linejoin: round;
}

.ss-content .ss-list {
  height: auto;
  flex: auto;
  overflow-x: hidden;
  overflow-y: auto;
}

.ss-content .ss-list .ss-error {
  color: var(--ss-error-color);
  padding: var(--ss-spacing-l);
}

.ss-content .ss-list .ss-searching {
  color: var(--ss-font-color);
  padding: var(--ss-spacing-l);
}

.ss-content .ss-list .ss-optgroup.ss-close .ss-option {
  display: none !important;
}

.ss-content .ss-list .ss-optgroup .ss-optgroup-label {
  padding: var(--ss-spacing-m) var(--ss-spacing-l) var(--ss-spacing-m) var(--ss-spacing-l);
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.ss-content .ss-list .ss-optgroup .ss-optgroup-label .ss-optgroup-label-text {
  color: var(--ss-font-color);
  flex: auto;
  font-weight: bold;
}

.ss-content .ss-list .ss-optgroup .ss-optgroup-label:has(.ss-arrow) {
  cursor: pointer;
}

.ss-content .ss-list .ss-optgroup .ss-optgroup-label .ss-optgroup-actions {
  justify-content: center;
  align-items: center;
  gap: var(--ss-spacing-m);
  flex-direction: row;
  flex: 0 auto;
  display: flex;
}

.ss-content .ss-list .ss-optgroup .ss-optgroup-label .ss-optgroup-actions .ss-selectall {
  cursor: pointer;
  flex-direction: row;
  flex: none;
  display: flex;
}

.ss-content .ss-list .ss-optgroup .ss-optgroup-label .ss-optgroup-actions .ss-selectall:hover {
  opacity: .5;
}

.ss-content .ss-list .ss-optgroup .ss-optgroup-label .ss-optgroup-actions .ss-selectall.ss-selected svg path {
  stroke: var(--ss-error-color);
}

.ss-content .ss-list .ss-optgroup .ss-optgroup-label .ss-optgroup-actions .ss-selectall span {
  text-align: center;
  padding: 0 var(--ss-spacing-s) 0 0;
  flex: 0 auto;
  justify-content: center;
  align-items: center;
  font-size: 60%;
  display: flex;
}

.ss-content .ss-list .ss-optgroup .ss-optgroup-label .ss-optgroup-actions .ss-selectall svg {
  width: 13px;
  height: 13px;
  flex: 0 auto;
}

.ss-content .ss-list .ss-optgroup .ss-optgroup-label .ss-optgroup-actions .ss-selectall svg path {
  fill: none;
  stroke: var(--ss-success-color);
  stroke-linecap: round;
  stroke-linejoin: round;
}

.ss-content .ss-list .ss-optgroup .ss-optgroup-label .ss-optgroup-actions .ss-selectall svg:first-child {
  stroke-width: 5px;
}

.ss-content .ss-list .ss-optgroup .ss-optgroup-label .ss-optgroup-actions .ss-selectall svg:last-child {
  stroke-width: 11px;
}

.ss-content .ss-list .ss-optgroup .ss-optgroup-label .ss-optgroup-actions .ss-closable {
  cursor: pointer;
  flex-direction: row;
  flex: 0 auto;
  display: flex;
}

.ss-content .ss-list .ss-optgroup .ss-optgroup-label .ss-optgroup-actions .ss-closable .ss-arrow {
  width: 10px;
  height: 10px;
  flex: auto;
}

.ss-content .ss-list .ss-optgroup .ss-optgroup-label .ss-optgroup-actions .ss-closable .ss-arrow path {
  fill: none;
